<template>
  <CCol col="12" xl="12">
    <CCard>
      <CCardHeader class="d-flex justify-content-between">
        <div>
          <slot name="header">
            <CIcon name="cil-healing" />
            <strong>Medicines</strong>
          </slot>
        </div>
        <CButton variant="outline" color="success" @click="openModalNewMed()">
          <CIcon name="cil-plus" class="mt-0" />&nbsp;Add New Medicine
        </CButton>
      </CCardHeader>
      <CCardBody>
        <v-client-table :data="rows" :columns="columns" :options="options">
          <div slot="actions" slot-scope="{ row }">
            <CButton
              class="mr-3"
              variant="outline"
              @click="openModalEdit(row)"
              color="primary"
            >
              Edit
              <CIcon name="cil-pencil" />
            </CButton>
            <CButton
              class="mr-3"
              variant="outline"
              @click="deleteRow(row)"
              color="danger"
            >
              Delete
              <CIcon name="cil-delete" />
            </CButton>
          </div>
        </v-client-table>
      </CCardBody>
    </CCard>
    <sweet-modal :blocking="Boolean(true)" ref="modal" overlay-theme="dark">
      <CCard style="text-align: left; border: 1px solid">
        <CCardHeader>
          <strong>{{ formName }}</strong>
        </CCardHeader>
        <CForm @submit.prevent="submit">
          <CCardBody>
            <p
              v-for="(error, index) in errors"
              :key="index"
              class="alert alert-danger"
            >
              {{ error }}
            </p>
            <CRow>
              <CCol md="6">
            <CInput
              label="Medicine Name"
              placeholder="Panadol"
              v-model.trim="$v.med.$model"
              :class="{ 'form-group--error': $v.med.$error }"
            />
            <div class="error" v-if="!$v.med.required">
              Medicine name is required
            </div>
              </CCol>
              <CCol md="6">
              <CInput
              label="Medicine Size"
              placeholder="500"
              v-model.trim="$v.size.$model"
              :class="{ 'form-group--error': $v.size.$error }"
            />
            <div class="error" v-if="!$v.size.required">
              Medicine size is required
            </div>
              </CCol>
            </CRow>
            <CInput
              label="Generic name"
              :class="{ 'form-group--error': $v.gen.$error }"
              v-model.trim="$v.gen.$model"
            />
            <div class="error" v-if="!$v.gen.required">
              Generic name is required
            </div>
            <CRow>
              <CCol md="6">
                <CSelect
                  label="Unit"
                  id="unitSelect"
                  placeholder="Select unit"
                  :options="units"
                  @change="changeUnit($event)"
                  v-model.trim="$v.unit.$model"
                  :class="{ 'form-group--error': $v.unit.$error }"
                />
                <div class="error" v-if="!$v.unit.required">
                  Unit is required
                </div>
              </CCol>
              <CCol md="6">
                <CSelect
                  label="Category"
                  id="categorySelect"
                  placeholder="Select category"
                  :options="categories"
                  @change="changeCategory($event)"
                  v-model.trim="$v.category.$model"
                  :class="{ 'form-group--error': $v.category.$error }"
                />
                <div class="error" v-if="!$v.category.required">
                  Category is required
                </div>
              </CCol>
            </CRow>
          </CCardBody>
          <CCardFooter>
            <CButton
              class="col"
              type="submit"
              variant="outline"
              color="success"
            >
              <CIcon name="cil-check-circle" />&nbsp;Submit
            </CButton>
          </CCardFooter>
        </CForm>
      </CCard>
    </sweet-modal>
  </CCol>
</template>

<script>
import { required } from "vuelidate/lib/validators";
export default {
  name: "Medicines",
  data() {
    return {
      med: "",
      gen: "",
      unit: "",
      id: "",
      size: "",
      errors: [],
      category: "",
      isEdit: false,
      units: [],
      categories: [],
      formName: "",
      rows: [],
      columns: [
        "name",
        "size",
        "generic_name",
        "unit.short_name",
        "category.name",
        "actions",
      ],
      options: {
        columnsClasses: { actions: "text-center" },
        filterByColumn: true,
        perPage: 10,
        texts: {
          filter: "Filter:",
          filterBy: "Filter by {column}",
          count: " ",
        },
        filterable: ["name", "generic_name"],
        headings: {
          name: "Name",
          "unit.short_name": "Unit",
          "category.name": "Category",
        },
      },
    };
  },
  validations: {
    med: {
      required,
    },
    unit: {
      required,
    },
    category: {
      required,
    },
    gen: {
      required,
    },
    size: {
      required,
    },
  },
  created() {
    this.getUnit_Category();
    this.getData();
  },
  methods: {
    submit() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.errors.push("Please check the form and submit.");
      } else {
        this.formData = {
          name: this.med,
          generic_name: this.gen,
          unit: this.unit,
          category: this.category,
          size: this.size,
        };
        if (this.isEdit === false) {
          this.$http
            .post("http://localhost:3000/medicine/post", this.formData)
            .then((res) => {
              this.getData();
              this.$swal.fire("Done!", "Medicine created successfully!", "success");
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        } else {
          this.$http
            .put("http://localhost:3000/medicine/" + this.id, this.formData)
            .then((res) => {
              this.getData();
              this.$swal.fire("Done!", "Medicine edited successfully!", "success");
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        }
        this.$refs.modal.close("modal");
      }
    },
    getData() {
      this.$http
        .get("http://localhost:3000/medicine/get")
        .then((res) => {
          this.rows = res.data;
          console.log(res.data);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    async getUnit_Category() {
      await this.$http
        .get("http://localhost:3000/unit/get")
        .then((res) => {
          let array = [];
          res.data.forEach((item) => {
            array.push(item.short_name);
          });
          this.units = array;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
      await this.$http
        .get("http://localhost:3000/category/get")
        .then((res) => {
          let array = [];
          res.data.forEach((item) => {
            array.push(item.name);
          });
          this.categories = array;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    deleteRow(row) {
      this.$http
        .delete("http://localhost:3000/medicine/" + row._id)
        .then((res) => {
          this.getData();
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    changeUnit(e) {
      this.unit = e.target.value;
    },
    changeCategory(e) {
      this.category = e.target.value;
    },
    async openModalEdit(row) {
      this.isEdit = true;
      this.id = row._id;
      this.$refs.modal.open("modal");
      this.formName = "Edit Medicine Form";
      this.med = row.name;
      this.gen = row.generic_name;
      this.unit = row.unit.short_name;
      this.category = row.category.name;
      this.size = row.size;
      this.errors = [];
    },
    async openModalNewMed() {
      this.isEdit = false;
      this.$refs.modal.open("modal");
      this.formName = "New Medicine Form";
      this.med = "";
      this.size = "";
      this.gen = "";
      let dropDown1 = document.getElementById("unitSelect");
      dropDown1.selectedIndex = 0;
      let dropDown2 = document.getElementById("categorySelect");
      dropDown2.selectedIndex = 0;
      this.errors = [];
    },
  },
};
</script>
<style scoped>
</style>